import React from 'react';
import './style.scss';

export default class InfoCountTotalCard extends React.Component {
  static defaultProps = {
    title: 'Card Title',
    icon: 'bullhorn',
    type: '',
    btnType: 'default',
    total: '',
    backgroundColor: '#0088FE',
    color: '#fff',
    href: '/',
  };

  state = {};

  render() {
    const { icon, type, title, total, backgroundColor, color, href } = this.props;
    const className = `infoCard ${type.length > 0 ? `infoCard--${type}` : ''}`;
    return (
      <div>
        <a href={href}>
          <span style={{ backgroundColor, color }} className={className}>
            {icon !== false && (
              <span className="infoCard__digit">
                <i className={`icmn-${icon}`} />
              </span>
            )}
            <span className="infoCard__desc">
              <span style={{ color }} className="infoCard__title infoCart_link">
                {title}
              </span>
              <p>Tổng: {total}</p>
            </span>
          </span>
        </a>
      </div>
    );
  }
}



